<template>
  <div class="messsagebox">
    <div class="mseleft">
      <div class="titlebox">
        <h3>留言区</h3>
        <span class="color"></span>
      </div>
      <el-input
        placeholder="请输入昵称"
        maxlength="10"
        show-word-limit
        prefix-icon="el-icon-user-solid"
        v-model="name"
        clearable
        autofocus="true"
      ></el-input>
      <el-input
        type="textarea"
        maxlength="60"
        show-word-limit
        placeholder="请输入内容"
        v-model="content"
        clearable
      ></el-input>
      <div class="btubox">
        <el-button type="primary" icon="el-icon-s-promotion" @click="postmessage">发表留言</el-button>
      </div>
      <div class="titlebox">
        <span class="color"></span>
        <h3>查找区</h3>
      </div>
      <el-input
        placeholder="输入昵称后即可查找相关留言"
        maxlength="10"
        show-word-limit
        prefix-icon="el-icon-search"
        v-model="serachName"
        clearable
      ></el-input>
      <div class="btubox searchbox">
        <el-button type="primary" icon="el-icon-search" @click="serachall">查看所有</el-button>
        <el-button type="primary" icon="el-icon-search" @click="serachmessage">立即查找</el-button>
      </div>
    </div>
    <div class="mseright">
      <div class="boxtitle">留言板</div>
      <div class="scrollbox">
        <vue-scroll :ops="ops">
          <div>
            <el-card class="box-card" v-for="(item, index) in messageArray" :key="index">
              <div slot="header" class="clearfix">
                <span class="userName" :class="{adminName:item.isAdmin}">{{item.name}}</span>
                <span class="date" :class="{adminDate:item.isAdmin}">{{item.date}}</span>
              </div>
              <div class="msgcont" :class="{admincont:item.isAdmin}">{{item.content}}</div>
            </el-card>
          </div>
        </vue-scroll>
      </div>
    </div>
  </div>
</template>
<script>
import { PostMessage, GetMessage } from "../../api/index";
export default {
  data() {
    return {
      name: "",
      content: "",
      serachName: "",
      crrentPage: 1,
      maxPage: 0,
      limit: 6,
      messageArray: [],
      ops: {
        //vuescroll的配置
        vuescroll: {
          sizeStrategy: "percent",
          detectResize: true
        },
        scrollPanel: {},
        rail: {},
        bar: {
          showDelay: 500,
          onlyShowBarOnScroll: false,
          keepShow: false,
          background: "#b3c0d1",
          opacity: 1,
          hoverStyle: false,
          specifyBorderRadius: false,
          minSize: false,
          size: "6px",
          disable: false
        }
      }
    };
  },
  created() {
    this.getmessage({ name: ""});
  },
  methods: {
    // 发布留言
    postmessage() {
      if (this.name == "" || this.content == "") {
        this.$notify.error({
          title: "错误",
          message: "昵称或者内容不能为空"
        });
      } else {
        PostMessage({ name: this.name, content: this.content }).then(res => {
          if (res.status) {
            this.$notify({
              title: "成功",
              message: res.msg,
              type: "success"
            });
            this.getmessage({ name: ""});
          } else {
            this.$message.error(res.msg);
          }
        });
      }
    },
    // 获取留言
    getmessage(args) {
      GetMessage(args).then(res => {
        console.log(res);
        if (res.status) {
          this.messageArray = res.data;
          this.name = "";
          this.content = "";
          this.serachName = "";
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    serachall() {
      this.getmessage({ name: ""});
    },
    // 查找留言
    serachmessage() {
      this.getmessage({ name: this.serachName });
    }
  }
};
</script>
<style lang="scss" scoped>
.messsagebox {
  margin: 20px;
  height: calc(100% - 40px);
  width: calc(100% - 40px);
  background: white;
  display: flex;
  .mseleft {
    background: white;
    padding: 20px;
    .el-textarea {
      margin: 20px 0px;
    }
    .btubox {
      text-align: right;
      margin-bottom: 40px;
    }
    .searchbox {
      margin-top: 20px;
    }
    .titlebox {
      position: relative;
      height: 50px;
      width: 200px;
      .color {
        position: absolute;
        height: 20px;
        width: 10px;
        background: rgb(10, 105, 168);
        top: 0px;
        left: -20px;
        z-index: 1;
      }
    }
  }
  .mseright {
    background: rgb(224, 226, 230);
    padding: 0px 10px;
    padding-top: 60px;
    position: relative;
    .boxtitle {
      background: white;
      width: 100px;
      border-radius: 5px;
      text-align: center;
      height: 40px;
      line-height: 40px;
      position: absolute;
      top: 10px;
      left: 10px;
      font-weight: bold;
      font-size: 18px;
      box-shadow: 0 5px 8px rgba(10, 16, 20, 0.24),
        0 0 8px rgba(10, 16, 20, 0.12);
    }
    .scrollbox {
      height: calc(100% - 10px);
      .el-card {
        margin: 10px 20px;
        .clearfix {
          display: flex;
          justify-content: space-between;
          .userName {
            font-size: 18px;
            font-weight: bold;
            color: rgb(121, 122, 122);
          }
          .adminName {
            color: rgb(50, 132, 170);
          }
          .date {
            color: rgb(206, 201, 201);
            font-size: 15px;
          }
          .adminDate{
            color: rgb(52, 117, 148);
          }
        }
        .msgcont {
          font-size: 15px;
          color: rgb(91, 95, 95);
        }
        .admincont {
          color: rgb(70, 72, 73);
        }
      }
    }
  }
}
.messsagebox > div {
  flex: 1;
}
</style>